import React from "react";
import img_Me from "../image/img-me.webp";

export default function About() {
  return (
    <div
      id="about"
      className="bg-white p-20 h-auto w-auto flex flex-col items-center justify-center"
    >
      <h1 className="text-4xl text-black text-center font-Comfortaa font-bold">
        About Me
      </h1>
      <img
        className="w-32 h-32 rounded-full mx-auto mt-10"
        src={img_Me}
        alt="Me"
      />
      <p className="text-black md:px-56 mt-10 mb-10 text-lg text-justify font-sans font-Nunito">
        I am a <b>Software Engineer</b> who mainly works on{" "}
        <b>Web Applications</b>, following new technologies very closely and trying to be up to date on
        the latest advancements. I have advanced knowledge in Artificial
        Intelligence and I love graphic design.
      </p>
    </div>
  );
}
