import React from "react";

import javaLogo from "../image/skills/java.svg";
import pythonLogo from "../image/skills/python.svg";
import reactLogo from "../image/skills/react.svg";
import angularLogo from "../image/skills/angular.svg";
import jsLogo from "../image/skills/javascript.svg";
import tsLogo from "../image/skills/typescript.svg";
import htmlLogo from "../image/skills/html.svg";
import cssLogo from "../image/skills/css.svg";
import firebLogo from "../image/skills/firebase.svg";
import unityLogo from "../image/skills/unity.svg";
import gameMLogo from "../image/skills/game-maker.svg";
import tfLogo from "../image/skills/tensorflow.svg";
import twLogo from "../image/skills/tailwind.svg";
import antDesign from "../image/skills/ant-design.svg";
import nodeJs from "../image/skills/nodejs.svg"
import express from "../image/skills/express.svg"
import flask from "../image/skills/flask.svg"
import typeorm from "../image/skills/typeorm.png"
import postgresql from "../image/skills/postgresql.svg"


export default function Skills() {
  return (
    <div
      id="skills"
      className="bg-white p-20 h-auto w-auto flex flex-col justify-center items-center font-Nunito"
    >
      <h1 className="text-4xl text-black text-center font-Comfortaa font-bold">Skills </h1>
      <h1 className="text-xl text-black text-bold text-center p-5">
        Technologies I have worked with:
      </h1>
      <div className="py-10 flex flex-wrap justify-items-center	items-center justify-center md:grid grid-cols-9 gap-2">
  
        <img className="w-20 h-20 m-1 mb-3 transition ease-in-out duration-700 hover:opacity-70" src={reactLogo} title="React" alt="React"/>
        <img className="w-20 h-20 m-1 mb-3 transition ease-in-out duration-700 hover:opacity-70" src={jsLogo} title="Javascript" alt="Javascript"/>
        <img className="w-20 h-20 m-1 mb-3 transition ease-in-out duration-700 hover:opacity-70" src={tsLogo} title="Typescript" alt="Typescript"/>
        <img className="w-20 h-20 m-1 mb-3 transition ease-in-out duration-700 hover:opacity-70" src={htmlLogo} title="HTML5" alt="Html"/>
        <img className="w-20 h-20 m-1 mb-3 transition ease-in-out duration-700 hover:opacity-70" src={cssLogo} title="CSS3" alt="Css"/>
        <img className="w-20 h-20 m-1 mb-3 transition ease-in-out duration-700 hover:opacity-70" src={nodeJs} title="Node.js" alt="Node.js"/>
        <img className="w-16 h-16 m-1 mb-3 transition ease-in-out duration-700 hover:opacity-70" src={express} title="Express.js" alt="Express"/>
        <img className="w-20 h-20 m-1 mb-3 transition ease-in-out duration-700 hover:opacity-70" src={antDesign} title="Ant Design" alt="Ant Design"/>
        <img className="w-20 h-20 m-1 mb-3 transition ease-in-out duration-700 hover:opacity-70" src={twLogo} title="Tailwind" alt="Tailwind"/>
        </div>
        <div className="flex flex-wrap justify-items-center	items-center justify-center md:grid grid-cols-4 gap-2">

        <img className="w-16 h-16 m-1 mb-3  filter grayscale transition ease-in-out duration-700 hover:opacity-70 hover:grayscale-0" src={angularLogo} title="Angular" alt="Angular"/>
        <img className="w-16 h-16 m-1 mb-3 ml-4 filter grayscale transition ease-in-out duration-700 hover:opacity-70 hover:grayscale-0" src={javaLogo} title="Java" alt="Java"/>
        <img className="w-16 h-16 m-1 mb-3  filter grayscale transition ease-in-out duration-700 hover:opacity-70 hover:grayscale-0" src={pythonLogo} title="Python" alt="Python"/>
        <img className="w-16 h-16 m-1 mb-3 ml-5 filter grayscale transition ease-in-out duration-700 hover:opacity-70 hover:grayscale-0" src={flask} title="Flask" alt="Flask"/>  
        <img className="w-16 h-16 m-1 mb-3  filter grayscale transition ease-in-out duration-700 hover:opacity-70 hover:grayscale-0" src={typeorm} title="TypeORM" alt="TypeORM"/>
        <img className="w-16 h-16 m-1 mb-3  filter grayscale transition ease-in-out duration-700 hover:opacity-70 hover:grayscale-0" src={postgresql} title="PostgreSQL" alt="PostgreSQL "/>
        <img className="w-16 h-16 m-1 mb-3  filter grayscale transition ease-in-out duration-700 hover:opacity-70 hover:grayscale-0" src={firebLogo} title="Firebase" alt="Firebase"/> 
        <img className="w-16 h-16 m-1 mb-3  filter grayscale transition ease-in-out duration-700 hover:opacity-70 hover:grayscale-0" src={tfLogo} title="Tensorflow" alt="Tensorflow"/>

        {/* <img className="w-16 h-16 m-1 mb-3  filter grayscale transition ease-in-out duration-700 hover:opacity-70 hover:grayscale-0" src={unityLogo} title="Unity" alt="Unity"/>
        <img className="w-16 h-16 m-1 mb-3  filter grayscale transition ease-in-out duration-700 hover:opacity-70 hover:grayscale-0" src={gameMLogo} title="GameMaker" alt="GameMaker"/> */}

      </div>
    </div>
  );
}
