import React from "react";
import img_elit from "../image/logo.png";

export default function Experience() {
  return (
    <div
      id="experience"
      className="bg-gray-900 p-20 h-auto w-auto flex flex-col items-center justify-center font-Nunito"
    >
      <h1 className="text-4xl text-white text-center font-Comfortaa font-bold">
        My Experience
      </h1>
      <h1 className="text-xl p-5 text-white ">Where I’ve Worked ?</h1>
      <div className="py-10 flex flex-wrap md:grid grid-cols-1 gap-8">
        <div className="p-2 flex flex-col items-center rounded overflow-hidden shadow-md card-inv">
          <h1 className="text-2xl font-semibold p-2 mt-5 text-black text-center">
            El Djazaïr Information Technology "ELIT"
          </h1>
          <img src={img_elit} alt="Elit" style={{ padding: "10px" }} />
          <p className="text-black text-justify mt-2">
            <b>Software Engineer - (Full Stack Web Dev)</b>
            <br/>
            <h5 className="text-center">(December 2021 - Present)</h5>
          </p>
          <p className="text-black text-justify p-10 pt-5 ml-5">
            &#x2022; Design and development of Web applications.
            <br />
            &#x2022; Added features and fixed bugs for GIS applications.
            <br />
            &#x2022; Update used technologies and security standards.
            <br />
            &#x2022; Finding the optimal solution/algorithm (storage/performance) to solve complex problems.
            <br />
            &#x2022; Communication with the client to collect their needs and understand
            their constraints.
            <br />
          </p>
        </div>
      </div>
    </div>
  );
}
